import React, { Component } from "react";
import FormKirimMasak from "./Component/FormKirimMasak";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

const columns = [
  {
    dataField: "no",
    text: "No",
    footer: "",
  },
  {
    dataField: "kode_jenis",
    text: "Kode Jenis",
    footer: "",
  },
  {
    dataField: "kadar_cetak",
    text: "Kadar Cetak(%)",
    footer: "",
  },
  {
    dataField: "bruto",
    text: "Bruto(Gr)",
    footer: (columnData) => columnData.reduce((acc, item) => acc + item, 0),
  },
  {
    dataField: "netto",
    text: "Netto(Gr)",
    footer: (columnData) => columnData.reduce((acc, item) => acc + item, 0),
  },
];

class KirimBahan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      tanggal: "",
      tukang: "",
      kode_barang: "",
      kadar_cetak: "",
      bruto: 0,
      netto: 0,
      no: 0,
      response: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    var data = {
      no: parseInt(this.state.no) + 1,
      kode_jenis: this.state.kode_barang,
      kadar_cetak: this.state.kadar_cetak,
      bruto: parseInt(this.state.bruto),
      netto: parseInt(this.state.netto),
    };
    var array = this.state.products;
    array.push(data);
    alert(JSON.stringify(this.state.products));
    this.setState({ response: "Success" });
    document.getElementById("kode_barang").focus();
    document.getElementById("kode_barang").value = null;
    document.getElementById("kadar_cetak").value = null;
    document.getElementById("bruto").value = null;
    document.getElementById("netto").value = null;
  };
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Kirim Bahan
                </h5>
                <div className="row">
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Tanggal</label>
                    <input
                      type="date"
                      id="tanggal"
                      name="tanggal"
                      className="form-control"
                      onChange={this.inputChange("tanggal")}
                    />
                  </div>
                  <div className="col-md-4 col-lg-3">
                    <label htmlFor="">Tukang</label>
                    <select
                      type="text"
                      id="tanggal"
                      name="tanggal"
                      className="form-control"
                      onChange={this.inputChange("tukang")}
                    >
                      <option value="AGNI">AGNI</option>
                      <option value="SOPO">SOPO</option>
                    </select>
                  </div>
                  <div
                    className="col-lg-1 col-md-1 no-gutters form-group"
                    style={{ marginLeft: -30 }}
                  >
                    <label htmlFor="" className="text-white">
                      .
                    </label>
                    <button
                      className="btn btn-primary form-control"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      +
                    </button>
                  </div>
                  <div className="col-md-5 col-lg-5"></div>
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Kode Barang</label>
                    <select
                      type="text"
                      id="kode_barang"
                      name="kode_barang"
                      className="form-control"
                      onChange={this.inputChange("kode_barang")}
                    >
                      <option value="KD001">KD001</option>
                    </select>
                  </div>
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Kadar Cetak</label>
                    <input
                      type="text"
                      id="kadar_cetak"
                      name="kadar_cetak"
                      className="form-control"
                      onChange={this.inputChange("kadar_cetak")}
                    />
                  </div>
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Bruto(Gr)</label>
                    <input
                      type="text"
                      id="bruto"
                      name="bruto"
                      className="form-control"
                      onChange={this.inputChange("bruto")}
                    />
                  </div>
                  <div className="col-md-3 col-lg-3">
                    <label htmlFor="">Netto</label>
                    <input
                      type="text"
                      id="netto"
                      name="netto"
                      className="form-control"
                      onChange={this.inputChange("netto")}
                    />
                  </div>
                  <div className="col-lg-12 col-md-12">
                    <div className="text-right">
                      <button
                        className="btn btn-primary mb-3 mt-3"
                        onClick={this.submit}
                      >
                        Tambah data
                      </button>
                    </div>
                  </div>
                </div>
                <BootstrapTable
                  keyField="id"
                  data={this.state.products}
                  columns={columns}
                  bordered={true}
                  headerClasses="header-table"
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button className="btn btn-primary">Simpan Data</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tukang Masak
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormKirimMasak />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default KirimBahan;
