import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
const columns = [
  {
    dataField: "city",
    text: "Kode Koderosok",
  },
  {
    dataField: "name",
    text: "Nama Koderosok",
  },
];
const product = [
  {
    city: "ADA",
    name: "ADA",
  },
];
class FormKirimMasak extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kode_tukang: null,
      nama_tukang: null,
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        <div className="col-md-2">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Kode Tukang
            </label>
            <input
              id="kode_provinsi"
              name="kode_provinsi"
              type="text"
              className="form-control"
              placeholder="KD001"
              onChange={this.inputChange("kode_tukang")}
            />
          </div>
        </div>
        <div className="col-md-10">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Nama Tukang
            </label>
            <input
              id="nama_provinsi"
              name="nama_provinsi"
              type="text"
              className="form-control"
              onChange={this.inputChange("nama_tukang")}
            />
          </div>
        </div>
        <div className="col-lg-12 col-md-12">
          <div className="text-right">
            <button
              name="submit"
              id="simpan"
              type="button"
              className="btn btn-info"
              onClick={this.submit}
            >
              Submit
            </button>
          </div>
        </div>
        <div className="col-lg-12 col-md-12">
          <ToolkitProvider
            keyField="id"
            data={product}
            columns={columns}
            search
          >
            {(props) => (
              <div className="form-group">
                <h3 className="mt-2">Kolom Pencarian</h3>
                <div>
                  <SearchBar
                    {...props.searchProps}
                    className="form-control"
                    placeholder="Tuliskan Kode / Nama"
                    style={{ margin: 0, top: "50%" }}
                  />
                </div>
                <hr />
                <BootstrapTable
                  {...props.baseProps}
                  striped
                  hover
                  condensed
                  bordered={true}
                  headerClasses="header-table"
                />
                <ExportCSVButton {...props.csvProps}>
                  <button className="btn btn-primary">Export CSV</button>
                </ExportCSVButton>
              </div>
            )}
          </ToolkitProvider>
        </div>
      </div>
    );
  }
}

export default FormKirimMasak;
